.price-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  background-color: transparent;
  border-bottom: 1px solid #afafaf;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  overflow: visible;
  padding: 5px 0;
}

.price-item:first-child {
  border: none;
}

.price-item:last-child {
  -webkit-animation: wat 1s ease-in-out;
          animation: wat 1s ease-in-out;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.price-item__text {
  -webkit-transition: scale 0.2s ease-in-out, background 0.1s ease-in-out;
  -o-transition: scale 0.2s ease-in-out, background 0.1s ease-in-out;
  transition: scale 0.2s ease-in-out, background 0.1s ease-in-out;
  pointer-events: none;
}

.price-item:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  border: 1px solid #afafaf;
   background-color: white;
}

@-webkit-keyframes wat {
  0% {background-color: red; opacity: 0;}
  100% {background-color:white; opacity: 1;}
}

@keyframes wat {
  0% {background-color: red; opacity: 0;}
  100% {background-color:white; opacity: 1;}
}


